import React from "react";
import Styles from "./OutputModal.module.css";

import { useSelector } from "react-redux";

// sample output
// {
//   "status": "success",
//   "exception": null,
//   "stdout": "Hello, World!\n",
//   "stderr": null,
//   "executionTime": 729,
//   "limitRemaining": 77006,
//   "stdin": ""
// }

const OutputModal = () => {
  const output = useSelector((state) => state.output);
  const codeStatus = useSelector((state) => state.codeStatus);

  return (
    //  Modal
    <div
      className="modal fade"
      id="exampleModalLong"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="exampleModalLongTitle"
      aria-hidden="true"
    >
      <div
        className="modal-dialog modal-dialog-centered modal-dialog-scrollable"
        role="document"
      >
        <div className={`modal-content ${Styles.modal}`}>
          <div className={`modal-header ${Styles.modal_header}`}>
            <h5 className="modal-title" id="exampleModalLongTitle">
              Output :{" "}
              <span>
                {codeStatus === "Running"
                  ? "Running"
                  : output?.exception !== null
                  ? "Error"
                  : `Finished in ${output?.executionTime}ms`}
              </span>
            </h5>
            <button
              type="button"
              // className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              X
            </button>
          </div>
          <div className="modal-body">
            {codeStatus === "Running" ? (
              <Spinner />
            ) : (
              <pre
                className={`${Styles.output} ${
                  output?.exception !== null && Styles.output_error
                }`}
              >
                {output?.exception !== null ? output?.stderr : output?.stdout}
              </pre>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

const Spinner = () => {
  return (
    <div className="d-flex justify-content-center">
      <div className="spinner-border" role="status">
        <span className="sr-only">Loading...</span>
      </div>
    </div>
  );
};

export default OutputModal;
