import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { changeFontSize, changeTheme } from "../../actions";
import DropDown from "../dropdown-btn/DropDown";
import Styles from "./SettingModal.module.css";

const themeOptions = [
  "Light",
  "Dark",
  "Solarized Light",
  "Solarized Dark",
  "Material Dark",
  "Gruvbox Dark",
  "Gruvbox Light",
];

const fontSizeOptions = [
  "14px",
  "15px",
  "16px",
  "17px",
  "18px",
  "19px",
  "20px",
];

const SettingModal = () => {
  const dispatch = useDispatch();
  const theme = useSelector((state) => state.theme);
  const fontSize = useSelector((state) => state.fontSize);

  return (
    <div
      className="modal fade"
      id="editorSettingsModal"
      tabIndex="-1"
      aria-labelledby="editorSettingsModal"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered">
        <div className={`modal-content ${Styles.modal}`}>
          <div className={`modal-header ${Styles.modal_header}`}>
            <h5 className="modal-title" id="exampleModalLabel">
              Code Editor Settings
            </h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div className="modal-body">
            <div className={Styles["setting__option"]}>
              <div className={Styles["setting__option--data"]}>
                <h5>Theme Options</h5>
                <p>
                  Tired of the white background? Try different styles and syntax
                  highlighting.
                </p>
              </div>
              <hr />
              <DropDown
                styles={{
                  backgroundColor: "var(--header-bg)",
                  color: "var(--text-color)",
                  fontSize: "0.8rem",
                }}
                value={theme}
                options={themeOptions}
                onSelect={(newTheme) => {
                  dispatch(changeTheme(newTheme));
                }}
              />
            </div>
            <hr />
            <div className={Styles["setting__option"]}>
              <div className={Styles["setting__option--data"]}>
                <h5>Font Size Options</h5>
                <p>Tired of small/large text?? Try different font size</p>
              </div>
              <hr />
              <DropDown
                styles={{
                  backgroundColor: "var(--header-bg)",
                  color: "var(--text-color)",
                  fontSize: "0.8rem",
                }}
                value={fontSize}
                options={fontSizeOptions}
                onSelect={(newFontSize) => {
                  dispatch(changeFontSize(newFontSize));
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingModal;
