export const changeLanguage = (newLanguage) => {
  return {
    type: "CHANGE_LANGUAGE",
    payload: newLanguage,
  };
};

export const changeCode = (newCode) => {
  return {
    type: "CHANGE_CODE",
    payload: newCode,
  };
};

export const changeInput = (newInput) => {
  return {
    type: "CHANGE_INPUT",
    payload: newInput,
  };
};

export const changeOutput = (newOutput) => {
  return {
    type: "CHANGE_OUTPUT",
    payload: newOutput,
  };
};

export const changeTheme = (newTheme) => {
  return {
    type: "CHANGE_THEME",
    payload: newTheme,
  };
};

export const changeFontSize = (newFontSize) => {
  return {
    type: "CHANGE_FONT_SIZE",
    payload: newFontSize,
  };
};

export const changeCodeStatus = (newCodeStatus) => {
  return {
    type: "CHANGE_CODE_STATUS",
    payload: newCodeStatus,
  };
};
