import React from "react";
import styles from "./Toolbar.module.css";
import SettingModal from "../setting-modal/SettingModal";
import { ReactComponent as RunIcon } from "../../assets/icons/play-arrow.svg";
import { ReactComponent as SettingsIcon } from "../../assets/icons/settings.svg";
import { ReactComponent as ResetIcon } from "../../assets/icons/refresh.svg";
import DropDown from "../dropdown-btn/DropDown";
import { useDispatch, useSelector } from "react-redux";
import {
  changeCodeStatus,
  changeLanguage,
  changeOutput,
  changeCode,
} from "../../actions";
import runCode from "../../services/codeRunner";
import getSupportedLanguages from "../../services/getSupportedLanguages";
import OutputModal from "../output-modal/OutputModal";

const supportedLanguages = getSupportedLanguages();
const languageOptions = Object.keys(supportedLanguages);

const Toolbar = () => {
  const dispatch = useDispatch();
  const language = useSelector((state) => state.language);
  const code = useSelector((state) => state.code);
  const input = useSelector((state) => state.input);

  async function handleRunCode() {
    dispatch(changeCodeStatus("Running"));
    const response = await runCode(
      code,
      input,
      language,
      supportedLanguages[language].file[0].name
    );
    console.log(response);
    dispatch(changeOutput(response));
    dispatch(changeCodeStatus("Finished"));
  }

  return (
    <div className={styles.toolbar}>
      <div className={`${styles.toolbar_items} ${styles.toolbar_left}`}>
        <DropDown
          styles={{
            backgroundColor: "var(--button-bg)",
            color: "var(--text-color)",
            fontSize: "0.8rem",
          }}
          value={language.toUpperCase()}
          options={languageOptions.map((language) => language.toUpperCase())}
          onSelect={(newLanguage) => {
            dispatch(changeLanguage(newLanguage.toLowerCase()));
          }}
        />

        <button
          className={styles.toolbar_button}
          data-bs-toggle="modal"
          data-bs-target="#editorSettingsModal"
        >
          <SettingsIcon />
        </button>
        <SettingModal />
        <button
          className={styles.toolbar_button}
          onClick={() => {
            const defaultCode = supportedLanguages[language].file[0].content;
            dispatch(changeCode(defaultCode));
          }}
        >
          <ResetIcon />
        </button>
      </div>
      <div className={styles.toolbar_items}>
        <button
          type="button"
          className={`${styles.toolbar_button} ${styles.run_button} btn`}
          data-bs-toggle="modal"
          data-bs-target="#exampleModalLong"
        >
          <RunIcon height={34} width={34} onClick={handleRunCode} />
        </button>
        <OutputModal />
      </div>
      <div className={styles.toolbar_items}>
        <a
          href="https://anshphirani.github.io/Codejs/"
          className={styles.redirect_button}
        >
          F. dev Playground
        </a>
      </div>
    </div>
  );
};

export default Toolbar;
